import React from 'react';

const LoginForm = () => {
    return (
    <>
        <div className="LoginForm">
            <div className="modal-wrapper">
                <div className="inp_text">
                    <label for="loginId" class="screen_out">아이디</label>
                    <input type="email" id="loginId" name="loginId" placeholder="ID"></input>
                </div>
                <div className="inp_text">
                    <label for="loginPw" class="screen_out">비밀번호</label>
                    <input type="password" id="loginPw" name="password" placeholder="Password"></input>
                </div>
            </div>
        </div>
        <style global jsx>
            {`
                .LoginForm {
                    width: 400px;
                    height: 600px;
                    margin: 0 auto;
                    top: 50%;
                    margin-top: 250px;
                }
                .modal-wrapper{
                    width: 400px;
                    background-color: #2A2D33;
                    border-radius: 40px;
                    font-family: $font;
                    color: #515866;
                    box-shadow: 0 40px 80px rgba(0,0,0,0.5);
                }
                .inp_text {
                    width: 200px;
                    margin: 0;
                    box-sizing: border-box;
                }
                .screen_out {
                    overflow: hidden;
                    line-height: 0;
                    text-indent: -9999px;
                    position: absolute;
                    width: 0;
                    height: 0;
                }
                .inp_text input {
                    line-height: 1.5;
                    font-family: Noto Sans DemiLight,AppleSDGothicNeo-Regular,Malgun Gothic,맑은 고딕,dotum,돋움,sans-serif;
                }
            `}
        </style>
    </>
    )
}

export default LoginForm;